* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	width: 100%;
	height: auto;
}

.App {
	width: 100%;
	height: auto;
	background-color: rgb(223, 223, 223);
}

ul {
	padding-left: 20px;
}

.textHomePage::first-letter {
	color: #1976d2;
	font-size: 20px;
	font-weight: 500;
}

.profil {
	width: 50%;
	object-fit: contain;
	max-width: 400px;
	transform: translate(50%, 10%);
}

.logo-project {
	width: 60%;
	max-width: 600px;
	object-fit: contain;
	opacity: 0;
}

.slideY-one {
	animation: slideY 1s ease-out 0.4s forwards;
}

.slideY-two {
	animation: slideY 1s ease-out 0.8s forwards;
}

.slideY-three {
	animation: slideY 1s ease-out 1.2s forwards;
}

@keyframes slideY {
	100% {
		opacity: 1;
		transform: translateY(-10px);
	}
}

.illustration {
	width: 100%;
}

.react {
	width: 20px;
	height: 20px;
	animation: parade 15s linear;
	position: absolute;
	opacity: 0.7;
}

@keyframes parade {
	0% {
		transform: translate(-700px, 600px);
	}

	30% {
		opacity: 0.5;
	}

	90% {
		opacity: 0.7;
	}

	100% {
		transform: translate(0px, 0px) rotateZ(3600deg);
		opacity: 0;
	}
}

.bottomNav {
	background-color: rgb(223, 223, 223);
	width: 100%;
	position: fixed;
	bottom: 0;
	left: 50%;
	transform: translateX(-50%);
}

.aaa {
	background-color: #004ba0;
	height: 100%;
	position: fixed;
	width: 0;
	top: 0;
	right: 0;
	opacity: 0;
	z-index: 1000;
	animation: a 0.2s linear forwards;
	padding-top: 20px;
}

@keyframes a {
	80% {
		width: 100%;
	}
	100% {
		width: 100%;
		opacity: 1;
	}
}

.openProject {
	position: absolute;
	top: 0%;
	right: 0%;
	width: 100%;
	height: 100%;
	background-color: white;
	transform: scale(1);
	transition: 0.3s;
	padding: 60px 0;
}

.closeProject {
	position: absolute;
	top: 0%;
	right: 0%;
	width: 100%;
	height: 100%;
	transform: scale(0);
	transition: 0.6s;
	opacity: 0;
}

.rotate {
	animation: rotation 4s linear infinite;
}

@keyframes rotation {
	100% {
		transform: rotateZ(120deg);
	}
}

.logoCard {
	width: 100%;
	opacity: 0.1;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
}

.description::first-letter {
	color: #1976d2;
	font-size: 40px;
	font-weight: 700;
}
